@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
body{font-family:'Balsamiq Sans', cursive}nav{padding:0;margin:0}#mainNav:hover+#extNav,#extNav:hover{width:400px;height:400px;border:1px solid black}#mainNav{position:absolute;cursor:pointer;width:200px;height:200px;background:#808e9b;background-blend-mode:multiply;border-radius:100%;align-content:center;vertical-align:middle;transform:translate(-45%, -45%);transition:.3s;box-shadow:0 0 2px 2px black;z-index:1}#mainNav img{position:absolute;width:100px;height:100px;right:10%;bottom:10%;transition:.3s}#about,#projects{display:flex;vertical-align:middle;align-content:center}#about i,#projects i{margin-right:5px}#extNav{position:relative;width:0;height:0;background-color:#b2bec3;border-radius:100%;top:0;left:0;transform:translate(-50%, -50%);transition:.5s;z-index:0}#extNav #projects{position:absolute;bottom:12%;right:20%}#extNav #about{position:absolute;top:60%;right:5%}#extNav div:hover{cursor:pointer;color:#3c40c6}

body{height:100%;width:100%}#moveBg{margin:0;position:absolute;width:100%;left:0;top:0;z-index:-10}#staticBg{position:fixed;bottom:0;left:0;height:100%;width:100%;z-index:-9;background-image:url(/CV/static/media/Pines&Mountains.6e918813.svg);background-position:bottom;background-repeat:no-repeat;background-size:100%}

.hideScrollBar{-ms-overflow-style:none;scrollbar-width:none}.hideScrollBar::-webkit-scrollbar{display:none}fieldset{position:relative;height:100%;width:100%;border:3px solid;border-image-slice:1;border-image-source:linear-gradient(to top, black, transparent 90%);padding:0;overflow:hidden}fieldset legend{height:30px;line-height:30px;width:100%;border-top:1px solid black;border-radius:80%;font-family:"Comfortaa", cursive;font-weight:800;font-size:1.2rem}fieldset .cardBg{position:relative;width:100%;height:100%;background:linear-gradient(to top, white, transparent)}fieldset .cardBg .textField{margin:auto;min-height:calc(90% - 20px);min-width:90%;max-height:calc(90% - 20px);max-width:90%;margin-top:20px;padding:10px;font-weight:500;line-height:2rem;overflow-x:hidden;overflow-y:scroll;white-space:pre-wrap}

#mainPageArticle{position:absolute;top:0;left:0;height:100%;width:100%;z-index:-5}#aboutCurrentProject{position:absolute;top:10%;right:15%;width:400px;height:400px;z-index:-5}#aboutProjectTechnologies{position:absolute;top:110%;left:15%;width:400px;height:400px;z-index:-5}@media screen and (max-width: 600px){#aboutCurrentProject,#aboutProjectTechnologies{width:50%;min-width:200px}}

body{overflow-y:scroll}#aboutPageArticle{z-index:-5;position:absolute;top:0;left:0;height:100%;width:100%}#aboutPageArticle>div{position:relative;overflow:scroll;display:flex;flex-direction:row;justify-content:space-between;padding:150px 10% 0 10%;height:calc(100% - 150px)}#personalInfo{flex-grow:3;height:600px;width:40%;min-width:400px;max-width:800px}#personalInfo .textField *{line-height:1.1rem;margin:0}#photoSection{margin-right:10%;margin-top:5%;flex-grow:1;height:0}#photoSection figure{width:calc(300px + 10px* 2 + 1px* 2);margin:0}#photoSection figure img{height:300px;width:300px;border-radius:10px;border:1px solid black;padding:10px;box-shadow:0 0 1px 1px #666}#photoSection figcaption{padding-top:10%;padding-bottom:10%;text-align:center;font-size:1.4rem;font-weight:600}@media screen and (max-width: 800px){#personalInfo,#photoSection{margin:auto}#personalInfo{height:300px}#photoSection{margin-bottom:10%}#photoSection figure{width:calc( 100px + 3px* 2 + 1px* 2);margin:0}#photoSection figure img{height:100px;width:100px;border-radius:10px;border:1px solid black;padding:3px;box-shadow:0 0 1px 1px #666}#photoSection figure figcaption{display:none}#aboutPageArticle>div{padding:0px;padding-top:5%;padding-bottom:10px;flex-direction:column}}

#projectsPage{z-index:-5;position:absolute;top:0;left:0;height:100%;width:100%}#projectsPage>div{position:relative;border-radius:5%;box-shadow:0 0 3px 3px black;background-color:#fff;opacity:0.7;margin:150px auto 0 auto;height:700px;max-width:50%;min-width:300px;overflow:hidden;overflow-y:scroll;-webkit-animation-name:turn;animation-name:turn;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-play-state:paused;animation-play-state:paused}#projectsPage>div ul.mainList{list-style-type:none;padding-left:5%;padding-right:5%}#projectsPage>div ul.mainList li{white-space:nowrap;overflow:hidden;cursor:pointer;position:relative;width:100%;height:50px;display:flex;align-items:center;justify-content:center;transition:0.5s}#projectsPage>div ul.mainList li .botBorder{position:absolute;border-bottom:1px dotted black;width:90%;left:5%;bottom:0}#projectsPage>div ul.mainList li:hover{box-shadow:0 0 2px 2px rgba(0,0,0,0.7)}#projectsPage>div ul.mainList li:hover .botBorder{display:none}#projectsPage>div .detailedProj{opacity:0;margin:5%;margin-top:2%;height:93%;color:black;-webkit-animation-name:appear;animation-name:appear;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-play-state:paused;animation-play-state:paused}#projectsPage>div .detailedProj .back{cursor:pointer;display:flex;align-items:center;justify-content:center;position:absolute;bottom:0;left:calc(50% - 51px);border:1px solid gray;border-radius:5%;padding-left:5px;height:40px;width:100px}#projectsPage>div .detailedProj .back:hover{background-color:rgba(128,128,128,0.479)}#projectsPage>div .detailedProj .text{padding-left:10px;padding-right:5px;height:90%;max-height:90%;overflow-y:scroll;border-bottom:1px solid black}@-webkit-keyframes turn{from{}to{transform:rotateY(360deg)}}@keyframes turn{from{}to{transform:rotateY(360deg)}}@-webkit-keyframes appear{from{opacity:0}20%{opacity:0}to{opacity:1}}@keyframes appear{from{opacity:0}20%{opacity:0}to{opacity:1}}@media screen and (max-height: 900px){#projectsPage>div{height:500px}}@media screen and (max-height: 650px){#projectsPage>div{height:300px}}

body{margin:0;padding:0;height:100%}

